export default {
	data() {
		return {
			userId: 'Hello',
			message: '获取验证码',
			codeStatus: false,
			second: 10,
			phone: '',
			code: '',
			responseMessage: ''
		}
	},
	onLoad() {

	},
	methods: {
		getCode: function(){
			if(this.verifyPhone()) {
				let _this = this;
				this.codeStatus = true;
				this.second = 10;
				let set = setInterval(function(){
					if(_this.second > 0) {
						_this.second --;
						_this.message = _this.second + 's';
					} else {
						_this.message = '获取验证码';
						_this.codeStatus = false;
						clearInterval(set);
					}
				}, 1000)
				//发送短信验证码接口
				uni.request({
					// http://haydwx.com/hawxwap/ywmobile/getCode.do
				    url: "http://haydwx.com/hawxwap/ywmobile/smsCode.do",
				    dataType: "JSON",
				    success: function(res) {
				        console.log(res)
				    }
				});
			}
		},
		verifyPhone: function(){
			// 验证手机号
			let reg = /^1[0-9]{10}$/
			if (this.phone == '' || this.phone.length <= 10 || !reg.test(this.phone)) {
				uni.showToast({
				    title: '手机号格式不正确',
				    duration: 2000,
					icon: 'none'
				});
				return false
			}
			return true
		},
		submitCode: function(){
			//提交验证码接口并校验是否通过认证
			uni.request({
			    url: "http://haydwx.com/hawxwap/ywmobile/getCode.do",
			    dataType: "JSON",
			    success: function(res) {
			        if(res.data) {
						responseMessage = '绑定成功'
					} else {
						responseMessage = '验证码错误'
					}
			    }
			});
		}
	}
}
